.main {
  width: 100%;
  padding-bottom: rem(60);
  overflow-x: hidden;

  @include media(md) {
    padding-bottom: rem(80);
  }

	&.no-overflow {
    overflow: unset;
	}

  &.offset {
    margin-top: rem(-49);

    @include media(md) {
      margin-top: rem(-106);
    }
  }

  &.offset--2 {
    margin-top: rem(-93);

    @include media(lg) {
      margin-top: rem(-100);
    }
  }

  &.video-anim {
    opacity: 1;
    transition: opacity 1s ease, transform 5s ease;
  }

  &.video-anim.animation {
    transform: translateY(100%);
    opacity: 0;
  }

  &.video-anim.animation--scale {
    transition: transform 1s ease, opacity 1s ease;
    transform: scale(1.5);
    transform-origin: top;
    opacity: 0;
  }

  &--bottom {
    padding-bottom: 0;
  }

  &__title {
    margin-bottom: rem(24);
    font-size: rem(28);
    line-height: rem(28);
    letter-spacing: rem(1.28);
    font-weight: 400;

    @include media(sm) {
      margin-bottom: rem(26);
    }

    @include media(md) {
      margin-bottom: rem(29);
      font-size: rem(40);
      line-height: rem(40);
    }

    @include media(lg) {
      margin-bottom: rem(34);
      font-size: rem(62);
      line-height: rem(62);
    }
  }

  &__section-title {
    line-height: rem(26);
    font-size: rem(24);
    font-weight: 400;

    @include media(sm) {
      font-size: rem(42);
      line-height: rem(44);
      letter-spacing: rem(1.28);
    }
  }

  &__text {
    &:not(:last-child) {
      margin-bottom: rem(64);

      @include media(md) {
        margin-bottom: rem(106);
      }

      @include media(lg) {
        margin-bottom: rem(100);
      }
    }
  }
}
